$cell-size: 100px;
$cell-gap: 1px;
$cell-with-gap: $cell-gap + $cell-size;
$calendar-padding: 10px;

$content-size: 7 * $cell-size + $calendar-padding + 7 * $cell-gap;
$cell-color: #eee;

$month-day: 31;

$days : (
  "mon": 0,
  "tue": 1,
  "wed": 2,
  "thu": 3,
  "fri": 4,
  "sat": 5,
  "sun": 6
)
